import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import image from "./background.png"
import axios from "axios"


async function user_login(userData) {
    const config = {
    method: 'post',
    // url: 'http://127.0.0.1:3002/api/auth/signin',
    url: 'https://klara-vercel.vercel.app/api/auth/signin',
    data: userData // Send userData as the request body
  };
  
try {
    const response = await axios(config);
    if (response.data.success) {
      if(response.data.data.isAdmin) {
        console.log("works ",response);
        return { isAdmin: true , response: true, data: response.data.data.places, regions: response.data.data.regions  };
      }
      return { isAdmin: false ,response: true, data: response.data.data.places };
    }  
    if (response.data.success == "false")
    {
      return {isAdmin: false , response: false, data: "Wrong Credential" };
    }
  } catch (error) {
    return { isAdmin: false ,response: "error", data: "Something is Wrong" };
  }
}


function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright  '}
      <Link color="inherit">
        Klara Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

// TODO remove, this demo shouldn't need to reset the theme.

const defaultTheme = createTheme();

export default function SignInSide({login, errorHandle, setHotels, setLoading, setRegions, setAdmin}) {
  const handleSubmit = async (event) => {
    setLoading((prev) => {return true})
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    const userData = {
      email: data.get('email'),
      password: data.get('password'),
    };
    const Info = await user_login(userData);
    console.log(Info);
    console.log("passed");
    if(Info.response === "error") {
      setLoading((prev) => {return false})
        console.log("Something is wrong");
        // login((prev) => {return false})

        errorHandle((prev)=> {return "Something is wrong, please check your connection"})
    }
    if(Info.response == true) {
        // console.log("Logged in", Info.data);

        if(Info.isAdmin) {
          // console.log("Admin Data",Info.regions);
          setRegions((prev) => {return Info.regions})
          setAdmin((prev) => {return true})
        }
        // localStorage.setItem('data', Info.data);
        setLoading((prev) => {return false})
        setHotels((prev) => {return Info.data})
        login((prev) => {return true})
        // window.sessionStorage.setItem("login", true);
    }
    if(Info.response == false) {
      setLoading((prev) => {return false})
        console.log("wrong credential");
        // login((prev) => {return false})
    }
  };

  return (
    <ThemeProvider theme={defaultTheme}>
      <Grid container component="main" sx={{ height: '100vh' }}>
        <CssBaseline />
        <Grid
        
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            backgroundImage: `url(${image})`,
            backgroundRepeat: 'no-repeat',
            backgroundColor: (t) =>
              t.palette.mode === 'light' ? t.palette.grey[50] : t.palette.grey[900],
            backgroundSize: 'cover',
            backgroundPosition: 'center',
          }}
        />
        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
              <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
              Sign in
            </Typography>
            <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 1 }}>
              <TextField
                margin="normal"
                required
                fullWidth
                id="email"
                label="Email Address"
                name="email"
                autoComplete="email"
                autoFocus
              />
              <TextField
                margin="normal"
                required
                fullWidth
                name="password"
                label="Password"
                type="password"
                id="password"
                autoComplete="current-password"
              />
              {/* <FormControlLabel
                control={<Checkbox value="remember" color="primary" />}
                label="Remember me"
              /> */}
              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
              >
                Sign In
              </Button>
              <Grid container>
                {/* <Grid item xs>
                  <Link href="#" variant="body2">
                    Forgot password?
                  </Link>
                </Grid> */}
                {/* <Grid item>
                  <Link href="#" variant="body2">
                    {"Don't have an account? Sign Up"}
                  </Link>
                </Grid> */}
              </Grid>
              <Copyright sx={{ mt: 5 }} />
            </Box>
          </Box>
        </Grid>
      </Grid>
    </ThemeProvider>
  );
}