import React, { useState } from 'react';
import './card.css'

import demo from './demo.jpg'
import icon from './icon.png'

// import './hotelinfo.css'

const Card = ({ backgroundImage, name,photos ,icon, address, handleCardClick, item, flagIcon }) => {
  // console.log(handleCardClick);
//  const apikey = "AIzaSyBWiQqf-QR374vWY1Y3tmkO7m7WO7RCUfI"
// const imgurl = "https://places.googleapis.com/v1/"+(photos? photos[0].name :'')+ "/media?maxHeightPx=400&maxWidthPx=400&key=" + apikey;
//  console.log("card",photos);
  return (
    <div className="card" onClick={() => handleCardClick(item)}>
      <div
        className="card__background"
        style={{ backgroundImage: `url(${photos})` }}
      ></div>
      <div className="card__content">
        <div className="card__header">
          <h2 className="card__name">{name}</h2>
        </div>
        <p className="card__address">{address}</p>
      </div>
    </div>
  );
};

Card.defaultProps = {
  backgroundImage: demo,
  name: 'Sheraton Addis',
  icon: icon,
  address: 'Ethiopia, Addis Ababa',
  flagIcon: 'default-flag-icon.png'
};

export default Card;
