import { useState, useEffect } from 'react';
import './App.css';
import SignUp from './Components/SignIn/AddUser';
import SignIn from './Components/SignIn/Signin';
import Container from './Components/Container/Containers';
import axios from "axios"
import HotelInformation from './Components/Detail/HotelInfo';
import SignInSide from './Components/SignIn/SideSignIn';
import Navbar from './Components/Navbar/Navbar';
import LoadingPopup from "./Components/Misc/Loading"
import Dropdown from './Components/Misc/DropDown';


async function getDetails(placeId) {
  const url = `https://klara-vercel.vercel.app/api/reviews/analysis/detail/${placeId}`;
  const config = {
    method: "get",
    url: url,
    // Send userData as the request body
  };
  const response = await axios(config);
  // if(response){
  console.log("(((((((((((((())))))))))))))))))");
  const data = response.data.data;
  // alert(response.data);
  const resultObject = {
    displayName: data.displayName,
    photo: data.imageUrl,
    address: data.formattedAddress,
    editorialSummary: data.editorialSummary,
    averageRating: data.rating,
    graph: data.ratings,
    summary: data.summary,
    detailSummary: data.classification,
  };
  console.log(data);
  console.log(resultObject);
  return resultObject;
}




function App() {
  const [userLogin, setUserLogin] = useState(false)
  const [loading, setLoading] = useState(false)
  const [isAdmin, setAdmin] = useState(false)

  const [regions, setRegions] = useState([])
  const [hotels, setHotels] = useState([])
  const [errorMessage, setErrorMessage] = useState("")
  const [hotelDetail, setHotelDetail] = useState({})
  const [showHotelInfo, setShow] = useState(false)
  const handleCardClick = async (itemID) => {
  setLoading((prev) => {return true})
  const hotelInfo = await getDetails(itemID)
  setShow(false)
  setHotelDetail((prev) => {return hotelInfo})
  setShow(true)
  setLoading((prev) => {return false})
}
  return (
    <div className="App">
      <Navbar logged= {setUserLogin} sanitize = {setHotels} admin= {setAdmin} />
      {/* <Dropdown options={["abebe", "kebede"]} /> */}
      {loading && <LoadingPopup />}
      {!userLogin && <SignInSide setLoading = {setLoading} login= {setUserLogin} errorHandle = {setErrorMessage} setHotels = {setHotels} setRegions = {setRegions} setAdmin = {setAdmin} />}
       
       {(hotels.length > 0 && !showHotelInfo) && <Container hotels={hotels} handleCardClick = {handleCardClick} regions = {regions} admin = {isAdmin} />}
       {/* {hotels.length > 0 && <Container hotels={hotels} handleCardClick = {handleCardClick} setHotelInfo = {setHotelInfo} />} */}
        {showHotelInfo && <HotelInformation Info={hotelDetail}  setShow = {setShow}  />}

    </div>
  );
}

export default App;
