import * as XLSX from 'xlsx'; // Import using correct syntax

function convertArrayToExcel(array) {
    // console.log(array.reviews);
  if (array.length <= 0) {
    throw new Error("Array must contain exactly four elements for the four reviews.");
  }
  else {
    const headers = ['Author Name', 'Rating', 'Food', 'Service', 'Environment'];
    const dataArray = array.reviews.map((review) => {
    return [
      review.author_name?.toString() || "N/A",
      review.rating?.toString() || "N/A",
      review.FOOD?.toString() || "N/A",
      review.SERVICE?.toString() || "N/A",
      review.ENVIRONMENT?.toString() || "N/A"
    ];
  });
  
//   console.log("Headers", headers.concat(dataArray));
  const worksheet = XLSX.utils.aoa_to_sheet([headers, ...dataArray])
//   console.log(worksheet);
  
  //   // Create worksheet with proper headers
  //   const worksheet = XLSX.utils.aoa_to_sheet([
      //     ["Review Category", "Summary"],  // Combined headers for clarity
      //     ["General Summary", array[0]],
      //     ["Food Review", array[1]],
      //     ["Service Review", array[2]],
      //     ["Environment Review", array[3]],
      //   ]);


  // Create, append sheet, and write to buffer
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Reviews");
    const buffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' }); // Use 'array' type

    // Create and download Excel file
    const blob = new Blob([buffer], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" });
    const link = document.createElement("a");
    link.href = URL.createObjectURL(blob);
    link.download = "reviews.xlsx";
    link.click();



//   const dataArray = data.reviews.map((review) => {
//     return [review.author_name, review.rating, review.FOOD, review.SERVICE, review.ENVIRONMENT];
//   });

  // Create, append sheet, and write to buffer
//   const workbook = XLSX.utils.book_new();
//   XLSX.utils.book_append_sheet(workbook, worksheet, "Reviews");
//   const buffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' }); // Use 'array' type

//   // Create and download Excel file
//   const blob = new Blob([buffer], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" });
//   const link = document.createElement("a");
//   link.href = URL.createObjectURL(blob);
//   link.download = "reviews.xlsx";
//   link.click();
  }

}

export default convertArrayToExcel;
