import React from 'react';
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
// import { faStar } from '@fortawesome/free-solid-svg-icons';
import convertArrayToExcel from '../Utils/newExcel';
import ReviewsTable from './ReviewTable';
import extractJSONFromString from '../Utils/JSONExtractor';
import "./hotelinfo.css"
import Rating from '@mui/material/Rating';


const HotelInformation = ({ Info,  setShow }) => {
  // console.log(Info);
  // console.log("pic",Info.photo.name);
  // const apikey = "AIzaSyBWiQqf-QR374vWY1Y3tmkO7m7WO7RCUfI"
  // const imgurl = "https://places.googleapis.com/v1/"+Info.photo.name+ "/media?maxHeightPx=400&maxWidthPx=400&key=" + apikey;
  const handleSubmit = (e) => {
    e.preventDefault();
    // Handle form submission here (e.g., sending email input to server)
  };
  return (
    <div className="hotel-information">
      <button className="go-back-button" onClick={() => { setShow((prev) => {return false}) }}>Go Back</button>
      <div className='nested-card'>
      <img src={Info.photo} alt="Hotel Photo" className="hotel-information__photo" />
      <div className="hotel-information__details">
        <h2 className="hotel-information__name">{Info.displayName}</h2>
        <p className="hotel-information__address">{Info.address}</p>
        <div className="hotel-information__rating">
          <p>Average Rating: {Info.averageRating}</p>
          <div className="star-icons">
            <Rating name="read-only" value={Info.averageRating} readOnly />
            {/* {Array.from({ length: Math.round(Info.averageRating) }, (_, index) => (
              // <FontAwesomeIcon icon={faStar} color='gold' key={index} />
            ))} */}
          </div>
        </div>
      </div>

            </div>
      <div className="hotel-information__text">
        <h3>Editorial Summary</h3>
        <p className="hotel-information__editorial-summary">{Info.editorialSummary}</p>
        <h3>Overall Summary</h3>
            <p className="hotel-information__summary">{Info.summary}</p>
          <h3>Detail Summary</h3>
          {/* {console.log(Info.detailSummary)} */}
             <ReviewsTable reviewsArray={extractJSONFromString(Info.detailSummary)}/>
            <div className='graph'>
                <h3>{Info.displayName} Graph Summary</h3>
                <h2>{console.log(Info.graph)}</h2>
                {/* <LineGraph/> */}
            </div>
        <div className="columns">
          
        </div>
      </div>
      <form onSubmit={handleSubmit} className="email-form">
        <input type="email" placeholder="Enter your email" className="email-input" />
        <button type="submit" className="submit-button">Send it in Email</button>
      </form>
      <button className="download-csv-button" onClick={() =>{
        convertArrayToExcel(extractJSONFromString(Info.detailSummary));
      }}>Download CSV</button>
    </div>
  );
};

export default HotelInformation;
