import './Nav.css'
import React from 'react';
import logo from './logo.png'
import axios from 'axios';

const logout = async (logged, sanitize, admin) =>{
  const config = {
    method: 'get',
    // url: 'http://127.0.0.1:3002/api/auth/signin',
    url: 'https://klara-vercel.vercel.app/api/auth/signout',
  };
  
try {
    const response = await axios(config);
    // console.log("works ",response);
    logged((prev) => { return false})
    sanitize((prev) => { return []})
    admin((prev) => { return false})
    if (response.data.success) {
      logged((prev) => { return false})
    }
  } catch (error) {
    return { response: "error", data: "Something is Wrong" };
  }
}


const Navbar = ({logged, sanitize, admin}) => {
  return (
    <nav className="navbar">
      <div className="navbar__logo">
        {/* Insert your logo component or image here */}
        <img src={logo} alt="Logo" />
      </div>
      <ul className="navbar__list">
        {/* Insert your navigation links here */}
        {/* <li className="navbar__item"><a href="#home">Home</a></li>
        <li className="navbar__item"><a href="#about">About</a></li>
        <li className="navbar__item"><a href="#services">Services</a></li> */}
        <li className="navbar__item" onClick={(e) => {logout(logged, sanitize, admin) }}>Sign Out</li>
      </ul>
    </nav>
  );
};

export default Navbar;
