import React, { useState } from 'react';
import './container.css';
import Card from '../Card/Card';
import addressExtractor from '../Utils/addressExtractor';
import Dropdown from '../Misc/DropDown';




function Container({ hotels, searchFor, setHotels, handleCardClick, admin , regions}) {
  
  const [selected, setSelected] = useState("all")
  const [fHotels, setFHotels] = useState([...hotels])
 
 
  console.log("For filterv ",fHotels);


const filterHotel = (selected) =>{
  if(selected == "all" || selected == "") 
    {
      setFHotels((prev) => {return hotels})
    }
    else {
      const filtered = hotels.filter((hotel) => selected == hotel.regionId)
      // const filtered = hotels.map((hotel) => {
      //   if(selected == hotel.regionId) {
      //     return hotel
      //   }
        
      // })
      setFHotels((prev)=>{return [...filtered]})
    }
}
console.log("Filtered hotels" , fHotels);
console.log("Filtered hotels selected" , selected);

  // Function to handle card click
//   const handleCardClick = (itemId) => {
//     // Perform actions when a card is clicked, e.g., navigate to a detailed view
//     console.log("Clicked on card with ID:", itemId);
//   };

  return (
    <div className="container">
      <div className="header">
        {/* <h3>Select Region </h3> */}
          {admin && <Dropdown options={regions} label={"Select Regions"} setSelected = {setSelected} filterHotel = {filterHotel}/>}
        {/* <button className="clear-search" onClick={() => { setHotels((prev) => ([])) }}>Clear Search</button> */}
      </div>
      <div className="Card-lists">
        {!admin &&
          hotels.map((item) => (
          
          <Card
            key={item.id}
            item={item.id}
            name={item.displayName}
            photos = {item.imageUrl}
            
            handleCardClick = {handleCardClick}
            address={addressExtractor(item.formattedAddress)}
            // address={""}
             // Adding onClick event handler
          />
        ))}
        {admin &&
          fHotels.map((item) => (
          
          <Card
            key={item.id}
            item={item.id}
            name={item.displayName}
            photos = {item.imageUrl}
            
            handleCardClick = {handleCardClick}
            address={addressExtractor(item.formattedAddress)}
            // address={""}
             // Adding onClick event handler
          />
        ))}
        
      </div>
    </div>
  );
}

export default Container;
